[data-screen="set"] {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column; }
  [data-screen="set"] .set-title {
    font-weight: 200;
    font-size: 45px;
    line-height: 60px; }
  [data-screen="set"] .set-summary {
    margin-top: 20px;
    font-weight: 300;
    font-size: 20px;
    line-height: 1.7;
    max-width: 920px; }
  [data-screen="set"] .set-projects {
    margin-top: 60px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap; }
    [data-screen="set"] .set-projects:hover .project {
      opacity: 0.6; }
    [data-screen="set"] .set-projects .project {
      display: flex;
      flex-direction: column;
      align-items: center;
      flex: 1 1 300px;
      height: 300px;
      cursor: pointer;
      transition: .6s;
      text-decoration: none;
      color: inherit; }
      [data-screen="set"] .set-projects .project:hover {
        opacity: 1; }
      [data-screen="set"] .set-projects .project .project-image {
        height: 200px;
        width: 200px;
        background: #ddd;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat; }
      [data-screen="set"] .set-projects .project .project-title {
        margin-top: 40px;
        font-size: 12px;
        font-weight: 400;
        letter-spacing: 3px;
        text-transform: uppercase; }
