[data-component="header"] {
  height: 130px;
  padding: 40px;
  display: flex;
  flex-direction: row;
  align-items: center; }
  [data-component="header"] .header-logo {
    color: inherit;
    text-decoration: none;
    margin-right: auto;
    font-size: 15px;
    /* background: #efefef; */
    /* color: white; */
    padding: 15px 0;
    font-weight: 600;
    letter-spacing: 3px;
    text-transform: uppercase;
    cursor: pointer;
    /* border: 1px solid #ddd; */ }
  [data-component="header"] .header-menu {
    margin-left: auto;
    color: black;
    font-weight: 400;
    transition: 1.5s; }
    [data-component="header"] .header-menu:hover {
      color: #888; }
