[data-screen="project"] {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  width: 100vw;
  padding-left: 0 !important;
  padding-right: 0 !important; }
  @media (max-width: 575px) {
    [data-screen="project"] {
      padding-top: 0 !important;
      padding-bottom: 0px !important; } }
  [data-screen="project"] .project-title {
    font-weight: 200;
    font-size: 45px;
    line-height: 60px; }
  [data-screen="project"] .project-summary {
    margin-top: 20px;
    font-weight: 300;
    font-size: 20px;
    line-height: 1.7;
    max-width: 920px; }
  [data-screen="project"] .project-info {
    height: 130px;
    padding: 40px 40px 0;
    display: flex;
    flex-direction: column;
    font-size: 12px;
    line-height: 1.7;
    font-weight: 500;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: inherit;
    text-decoration: none; }
    [data-screen="project"] .project-info .info-set {
      font-weight: 700;
      color: inherit;
      text-decoration: none; }
  [data-screen="project"] .project-images {
    flex: 1 1 0;
    display: flex;
    position: relative;
    max-width: 100vw; }
    [data-screen="project"] .project-images .image-box-container {
      flex: 1 1 0;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: flex-start;
      transition: .6s;
      position: relative;
      overflow: visible; }
      @media (max-width: 575px) {
        [data-screen="project"] .project-images .image-box-container {
          transition: .3s; } }
    [data-screen="project"] .project-images .image-prev {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      width: 50%;
      z-index: 1; }
    [data-screen="project"] .project-images .image-next {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      width: 50%;
      z-index: 1; }
    [data-screen="project"] .project-images .image-box {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      flex: 0 0 100vw;
      height: auto;
      cursor: pointer;
      padding: 0 40px;
      max-height: 60vh; }
      @media (max-width: 575px) {
        [data-screen="project"] .project-images .image-box {
          flex: 0 0 calc(100vw);
          padding: 0 0px; } }
      [data-screen="project"] .project-images .image-box .box-image {
        height: 60vh;
        width: 100%;
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        position: relative; }
