[data-component="footer"] {
  background: #f2ae0d;
  background: #000;
  color: white;
  justify-self: flex-end;
  margin-top: auto; }
  [data-component="footer"] .footer {
    min-height: 300px;
    display: flex;
    flex-direction: row;
    padding: 40px 0;
    flex: 1 0 auto; }
    [data-component="footer"] .footer .footer-title {
      flex: 2 1 300px;
      font-size: 33px;
      font-weight: 100;
      line-height: 1;
      text-transform: uppercase;
      align-self: flex-end; }
    [data-component="footer"] .footer .footer-links {
      flex: 1 1 250px; }
      [data-component="footer"] .footer .footer-links .footer-link {
        font-size: 14px;
        font-weight: 400;
        letter-spacing: 2px;
        text-transform: uppercase;
        line-height: 1.2;
        font-family: 'Hind Vadodara', sans-serif;
        height: 40px;
        display: flex;
        flex-direction: row;
        align-items: center;
        cursor: pointer;
        transition: .2s;
        user-select: none;
        text-decoration: none; }
        [data-component="footer"] .footer .footer-links .footer-link:hover {
          color: #f3b520;
          transform: translateY(-2px); }
        [data-component="footer"] .footer .footer-links .footer-link:hover {
          color: white; }
