[data-component="newsletter-popup"] {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
  transition: 1.5s; }
  [data-component="newsletter-popup"][data-active="true"] {
    z-index: 2000; }
    [data-component="newsletter-popup"][data-active="true"] .newsletter-popup-background {
      visibility: visible;
      opacity: 1;
      z-index: 2; }
    [data-component="newsletter-popup"][data-active="true"] .newsletter-popup {
      visibility: visible;
      opacity: 1;
      z-index: 3;
      transform: translateX(0); }
  [data-component="newsletter-popup"] .newsletter-popup-background {
    visibility: hidden;
    opacity: 0;
    z-index: -1;
    transition: 1.5s;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.5);
    cursor: pointer; }
  [data-component="newsletter-popup"] .newsletter-popup {
    position: fixed;
    transform: translateX(-30px);
    visibility: hidden;
    opacity: 0;
    z-index: -1;
    transition: 1.5s;
    width: 300px;
    height: 300px;
    background: white;
    box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.15);
    padding-top: 50px; }
