[data-component="image"] .image-box {
  width: calc(100% + 160px);
  margin-left: -80px;
  margin-bottom: 40px;
  background: white;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.15); }

[data-component="image"] .normal-image-box {
  background: white;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.15); }

[data-component="image"] .image {
  position: relative; }
  [data-component="image"] .image .image-caption {
    position: absolute;
    background: white;
    box-shadow: 5px 5px 25px rgba(0, 0, 0, 0.1);
    padding: 10px 40px;
    left: -20px;
    bottom: 20px;
    z-index: 5;
    max-width: calc(50%);
    background: #333;
    background: #f2ae0d;
    color: white;
    font-size: 12px;
    font-weight: 600;
    letter-spacing: 1px;
    text-transform: uppercase;
    line-height: 1.2;
    font-family: 'Hind Vadodara', sans-serif; }
  [data-component="image"] .image img {
    width: 100%; }
  [data-component="image"] .image .image-element {
    height: 450px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat; }
